.mainProduct{
    display: grid;
    grid-template-columns: repeat(auto-fill,300px);
    justify-content: center;
    gap: 20px;
    text-transform: uppercase;
    .product{
        position: relative;
        padding-top: 10px;
        padding-bottom: 20px;
        border-radius: 20px;
        box-shadow: 1px 0px 10px #0000000c;
        .top{
            .bx-heart{
                position: absolute;
                right: 20px;
                top: 10px;
                font-size: 20px;
            }
            .bxs-heart{
                position: absolute;
                right: 20px;
                top: 10px;
                font-size: 20px;
                color: red;
                animation: like .5s ease-in;
            }
        }
        .bottom{
            display: flex;
            justify-content: center;
            // background: red;
            .text{
                width: 100%;
                padding: 0 10px;
                .name{
                    width: 100%;
                    // background: yellow;
                    display: flex;
                    justify-content: space-between;
                    h2{
                        font-family: $font-family;
                    }
                    h3{
                        font-size: 16px;
                        font-weight: 400;
                        color: rgb(65, 65, 65);
                    }
                }
                .desc{
                    // background: blue;
                    text-align: left;
                    .about{
                        font-size: 18px;
                        margin-top: -5px;
                        text-transform: capitalize;
                    }
                    .rating{
                        color: yellow;
                        span{color: gray;}
                    }
                    button{
                        padding: 10px 20px;
                        border: none;
                        outline: none;
                        background: $primary-bg;
                        color: $font-color;
                        font-weight: $font-bold;
                        font-size: 15px;
                        border-radius: 30px;
                        margin-top: 30px;
                        cursor: pointer;
                    }
    
                }
            }
        }
    }
}