.footer{
    min-height: max-content;
    width: 100%;
    // background: #e8e8e8;
    color: #000000;
    display: flex;
    align-items: center;
    flex-direction: column;
    hr{
        width: 95%;
        background: #000;
        height: 3px;
        border: none;
        outline: none;
        margin-top: -5px;
    }
    .main{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align:left;
        .left{
            width: 30%;
            padding: 0 30px;
            h1{
                font-size: 2.5rem;
                font-family: $logo-font;
            }
            p{
                font-size: 1.4rem;
                color: $link-color;
                margin-top: -10px;
                text-transform: capitalize;
            }
        }
        .right{
            height: 100%;
            width: 60%;
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            .box{
                display: flex;
                flex-direction: column;
                a{
                    display: flex;
                    align-items: center;
                    text-decoration: none;
                    color: $link-color;
                    font-weight: 400;
                    margin-top: 10px;
                    font-size: 1.2rem;
                    .bx{
                        font-size: 1.5rem;
                        padding: 0 10px;
                    }
                    &:hover{
                        color: #605f5f;
                    }
                }
            }
        }
    }
    .foot{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        .left{
            padding-left: 40px;
            img{
                width: 40px;
                margin-right: 10px;
                cursor: pointer;
            }
        }
        .right{
            padding-right: 40px;
            p{
                font-weight: $font-bold;
                color: #605f5f;
            }
        }
    }
}
@media screen and (max-width:580px){
    .footer{
        .main{
            display: flex;
            flex-direction: column;
            .left{
                width: 100%;
                padding-left: 80px;
                p{
                    width: 80%;
                }
            }
            .right{
                width: 100%;
                padding: 20px 0;
            }
        }
        .foot{
            text-align: center;
            justify-content: center;
            padding: 0;
            .left{
                margin: 0;
                padding: 0;
            }
            .right{
                padding: 0;
                margin: 0;
            }
        }
    }
}