.categories{
    min-height: 70vh;
    width: 100%;
    padding-bottom: 30px;
    a{
        text-decoration: none;
    }
    hr{
        height: 3px;
        border: none;
        width: 95%;
        background: #f1f1f1;
        border-radius: 30px;
    }
    h1{
        text-align: left;
        margin-left: 40px;
        color: #000;
    }
    .main{
        display: grid;
        grid-template-columns: repeat(auto-fit,400px);
        justify-content: center;
        gap: 25px;
        .categorie{
            height: 100px;
            background: #f8f8f8;
            display: flex;
            align-items: center;
            padding: 10px 20px;
            border-radius: 10px;
            &:hover{
                cursor: pointer;
                background: #f2f2f2;
                transition: background .3s;
                box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.165);
            }
            .left{
                height: 100%;
                width: 30%;
                img{
                    height: 100%;
                    width: 100%;
                    object-fit: contain;
                }
            }
            .right{
                text-align: left;
                margin-left: 10px;
                .title{
                    text-align: left;
                    margin-left: 0;
                    text-transform: capitalize;
                    font-size: 25px;
                }
                .desc{
                    margin-top: -10px;
                    font-weight: $font-bold;
                    color: $link-color;
                }
            }
        }
    }
}
// tablet 
@media screen and (max-width:850px) {
    .categories{
        .main{
            grid-template-columns: repeat(auto-fit,250px);
            gap: 15px;
            .categorie{
                text-align: center;
                &:hover{
                    box-shadow: none;
                }

                .right{
                    // margin: 0;
                    p{
                        display: none;
                    }
                    .title{
                        font-weight: 700;
                        text-align: center;
                    }
                }
            }
        }
    }
}
@media screen and (max-width:500px) {
    .categories{
        .main{
            grid-template-columns: repeat(auto-fit,370px);
            gap: 15px;
            .categorie{
                text-align: center;
                &:hover{
                    box-shadow: none;
                }

                .right{
                    // margin: 0;
                    p{
                        display: none;
                    }
                    .title{
                        font-weight: 700;
                        text-align: center;
                    }
                }
            }
        }
    }
}
// tablet 
@media screen and (max-width:400px) {
    .categories{
        .main{
            grid-template-columns: repeat(auto-fit,150px);
            gap: 15px;
            .categorie{
                text-align: center;
                &:hover{
                    box-shadow: none;
                }
                img{
                    display: none;
                }
                .right{
                    margin: 0;
                    margin-left: -10px;
                    p{
                        display: none;
                    }
                    .title{
                        font-size: 18px;
                        text-align: center;
                    }
                }
            }
        }
    }
}
