.navigation{
    width: 100%;
    // background: #f1f1f1;
    position: absolute;
    top: 0;
    header{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .miniNav{
            position: fixed;
            top: 0;
            height: 30px;
            background: #000;
            width: 100%;
            color: $font-color;
            font-weight: $font-bold;
            z-index: 20;
            p{
                margin-top: 5px;
            }
        }
        .navbar{
            margin-top: 30px;
            width: 100%;
            height: 70px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .menuBtn{
                padding: 10px;
                margin-right: 20px;
                cursor: pointer;
                display: none;
                .bx-menu{
                    font-size: 2rem;
                }
            }
            .left{
                padding: 0 30px;
                font-family: $logo-font;
                font-size: 1.2rem;
                
            }
            .right{
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-right: 20px;
                margin: $nav-space;
                // .select{
                //     background: transparent;
                //     border: none;
                //     outline: none;
                //     font-size: 16px;
                //     font-weight: $font-bold;
                //     margin: $nav-space;
                //     option{
                //         height: 20px;
                //     }
                // }
                .links{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin: $nav-space;
                    a{
                        text-decoration: none;
                        color: $link-color;
                        font-weight: $font-bold;
                        padding: 10px;
                    }
                }
                .searchBox{
                    margin: $nav-space;
                    position: relative;
                    input{
                        height: 40px;
                        width: 250px;
                        padding: 0 10px;
                        outline: none;
                        border:none;
                        border-radius: 30px;
                    }
                    .bx-search{
                        position: absolute;
                        right: 10px;
                        top: 10px;
                        font-size: 1.5rem;
                        font-weight: $font-bold;
                        cursor: pointer;
                    }
                }
                .cart-user{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    .bx-user{
                        margin: $nav-space;
                        font-size: 1.2rem;
                        color: $font-color;
                        background: $primary-bg;
                        padding: 10px;
                        border-radius: 50%;
                    }
                    .bx-cart{
                        margin: $nav-space;
                        font-size: 1.2rem;
                        color: $link-color;
                        &::after{
                            content: '';
                            position: absolute;
                            height: 10px;
                            width: 10px;
                            background: rgb(134, 220, 4);
                            border-radius: 50%;
                        }

                    }
                }
            }
        }
    }
}

// tablet size 
@media screen and (max-width:1300px) {
    .navigation{
        header{
            .navbar{
                .right{
                  margin: $tablet-space;
                  .select{
                    margin: $tablet-space;
                  }  
                  .links{
                    margin: $tablet-space;
                  }
                  .searchBox{
                    margin: $tablet-space;
                    input{
                        width: 180px;
                    }
                }
                .cart-user{
                    .bx-user{
                    margin: $tablet-space;
                    }
                    .bx-cart{
                    margin: $tablet-space;
                    }
                }
                }
            }
        }
    }
}
// tablet size 
// mobile size 
@media screen and (max-width:850px) {
    .navigation{
        *{
            margin: 0;
        }
        header{
            .navbar{
                padding-right: 0;
                .menuBtn{
                    display: block;
                }
                .right.showMenu{
                    display: flex;
                    transition:.3s;
                    width: 97%;
                    opacity: 1; 
                    right:0;
                }
                .right{
                    // display: none;
                    opacity: 0;
                    position: absolute;
                    top: 100px;
                    right: -80px;
                    height: 83vh;
                    width: 0%;
                    padding: 5px 20px;
                    background: #00000095;
                    backdrop-filter: blur(10px);
                    justify-content: space-around;
                    flex-direction: column-reverse;
                    margin: 0;
                    transition:.3s;
                    .select{
                        margin-top: -50px;
                        color: $font-color;
                        option{
                            color: $link-color;
                        }
                    }
                    .links{
                        flex-direction: column;
                        margin: 0;
                        margin-top: -20px;
                        a{
                            text-align: left;
                            font-size: 20px;
                            color: $font-color
                        }
                    }
                    .searchBox{
                        margin-left: 50px;
                        input{
                            width:300px;
                        }
                    }
                    .cart-user{
                        .bx-cart{
                            color: $font-color;
                            font-size: 23px;
                        }
                    }
                }
            }
        }
    }
}
// mobile size 
:root {
    --select-border: #777;
    --select-focus: rgb(71, 169, 187);
    --select-arrow: var(--select-border);
  }
select {
    // appearance: none;
    border: none;
    padding: 0 1em 0 0;
    margin: 0;
    width: 100%;
    font-family: inherit;
    font-size: inherit;
    cursor: inherit;
    line-height: inherit;
    outline: none;
  }
  select::-ms-expand {
    display: none;
  }
  .select {
    width: 100%;
    min-width: 15ch;
    max-width: 30ch;
    border-radius: 0.25em;
    padding: 0.15em 0.1em;
    font-size: 1.1rem;
    cursor: pointer;
    line-height: 1.1;
    color: #0d0d0d;
    background-color: transparent;
    box-shadow: none;
    background-image: linear-gradient(to top, #f9f9f9, #fff 33%);
  }
 