.products{
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    background: #fff;
    padding-bottom: 50px;
    text-transform: capitalize;
    h1{
        text-align: left;
        margin-left: 30px;
        text-transform: capitalize;
    }

}
@keyframes like {
    0%{
    text-shadow: 0 0 10px rgba(255, 0, 0, 0.912);
    }
    50%{
    text-shadow: 0 0 20px rgba(255, 0, 0, 0.849);
    }
100%{
    text-shadow: 1px 1px 30px rgba(255, 0, 0, 0.751);
    }
}