$font-family:sans-serif;
$logo-font:futura;
$font-color:#fff;
// $primary-bg:linear-gradient(95deg,#ff0084,#fffc00);
// $primary-bg:linear-gradient(45deg,#aa076b,#61045f);
$primary-bg:linear-gradient(95deg,#00d2ff,#3a7bd5);
$link-color:#2e2e2e;
$font-bold:600;
$nav-space:0 15px;
$tablet-space:0 10px;
html{
    overflow-x: hidden;
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    width: 0;
}
body{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
    text-align: center;
}
.bx{
    cursor: pointer;
}
button:active{
    transform: translateY(3px);
    box-shadow: 0px 0px 10px #0000002c;
}

