.headerMain{
    min-height: 60vh;
    margin-top: 80px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    .left{
        min-height: 300px;
        width: 50%;
        padding: 30px 60px;
        text-align: left;
        font-family: $logo-font;
        h1{
            font-size: 3.3rem;
        }
        button{
            padding: 15px 50px;
            border: none;
            outline: none;
            background: $primary-bg;
            color: $font-color;
            font-weight: $font-bold;
            font-size: 15px;
            border-radius: 30px;
            margin-top: 30px;
            cursor: pointer;
        }
    }
    .right{
        width: 50%;
        height: 400px;
        margin-top: 50px;
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}

@media screen and (max-width:900px){
    .headerMain{
        flex-direction: column;
        .left{
            width: 100%;
            button{
                margin-left: 30px;
            }
            h1{
                font-size: 4rem;
                padding: 10px 30px;
            }
        }
        .right{
            width: 100%;
        }
    }
}
@media screen and (max-width:600px){
    .headerMain{
        flex-direction: column;
        .left{
            width: 100%;
            button{
                margin-left: 30px;
                padding: 20px 70px;
            }
            h1{
                font-size: 3rem;
                padding: 10px 30px;
            }
        }
        .right{
            width: 100%;
        }
    }
}