.cart{
    font-family:sans-serif;
    min-height: 100vh;
    position: fixed;
    top: 0;
    width: 100%;
    background: #00000090;
    backdrop-filter: blur(10px);
    z-index: 50;
    text-align: left;
    display: none;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    h1{
        font-family: futura;
    }
    h3{
        font-weight: 600;
        color: #424242;
    }
    p{
        font-size: 17px;
        color: #3e3e3e;
        font-weight: 400;
    }
    .main{
        background: #fff;
        width: 95%;
        display: flex;
        align-items: center;
        justify-content: center;
        img{width: 50px;}
        .left{
            height: 100%;
            width: 40%;
            img{width: 400px;}
        }
        .right{
            width: 50%;
            .top{
                display: flex;
                p{
                    width: 400px;
                }
                .bx{
                    position: absolute;
                    right: 50px;
                    top: 50px;
                    font-size: 2rem;
                }
            }
            .center{
                text-transform: uppercase;
                display: flex;
                justify-content: space-between;
                .left{
                    padding: 10px 20px;
                }
                .right{
                    padding: 10px 20px;
                }
            }
            .bottom{
                img{
                    margin-right: 20px;
                    cursor: pointer;
                }
            }
        }
    }
}

.cart.active{
    display: flex;
}